// see `./src/styles/layout/container.scss`
// for margin modifications on this module's parent container

.callout-button-block {
  @include null-margins;
  border-top: 1px solid $border--light;
  font: 1.6rem/1.7 $font-family--serif;
  margin: unit(1) auto 0;
  padding-top: unit(1);
  text-align: center;

  .-light-gray & {
    border-top-color: rgba($color--tuatara, 0.1);
  }

  .-dark-gray & {
    border-top-color: rgba($color--white, 0.3);
    color: $color--white;
  }

  @include respond-to('medium-large') {
    font-size: 1.8rem;
    margin-top: unit(2);
  }
}

.callout-button-block__buttons {
  .button {
    @media (max-width: map-get($breakpoints, 'medium--down')) {
      display: block;
      margin-left: auto;
      margin-right: auto;
      max-width: unit(22);
    }

    + .button {
      margin-top: unit(0.5);

      @include respond-to('medium') {
        margin-left: unit(0.5);
        margin-top: 0;
      }
    }
  }
}
